*, *:before, *:after {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.firstForm, .secondForm, .thirdForm {
    margin-top: 45px;
    margin-left: 40%;
}

.field {
    width: 100%;
    height: 760px;
    background-color: burlywood;
    border: 1px solid aquamarine;
}

input {
    width: 300px;
    height: 50px;
}

button {
    width: 300px;
    height: 50px;
    margin-left: 40%;
    margin-top: 75px;
}

.secondField {
    width: 100%;
    height: 760px;
    background-color: blue;
    margin-top: 0px;
}

.gloria {
    border: 1px solid blue;
}

.glory {
    margin-top: 290px;
    font-size: 160px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    color: yellow;
}